<template>
  <div id="patient-detail" class="flex-col">
    <div class="hearder flex-row" style="align-item: center;" :style="{width: width+'px'}">
      <div class="task flex-row">
        <span :class="{active: currentTaskId === task.taskid ? true : false}" v-text="task.taskname" v-for="task in taskList"></span>     
      </div>
      <div class="check" v-if="!(status === '4' || status === '3')">
        <dropdown>
          <div class="btn-gray tool" slot="trigger"><i>审核操作</i><i class="iconfont icon-xiasanjiao-copy"></i></div>
          <li @click="checkPass('4')">通过</li>
          <li @click="backDialog = true">退回</li>
        </dropdown>
      </div>
    </div>
    <!--退回病例对话框-->
    <DialogBox v-if="backDialog" :width="400">
      <div slot="header">病历审核</div>
      <div slot="body" class="body flex-col">
        <span>您确定将此病例退回？</span>
        <span>如果确定，请填写退回原因（也可不填）</span>
        <textarea v-model="backText" style="height: 100px;"></textarea>
      </div>
      <div slot="footer" class="flex-row">
        <span class="btn-gray mg-r-8" @click="backDialog = false">取消</span>
        <span class="btn-blue" @click="checkBack('3')">提交</span>
      </div>
    </DialogBox>
    <!--end-->
    <div class="patient-taskform" id="patient-taskform" :style="{width: width + 'px'}">
      <div style="background: #fff;" v-if="back">
        <div class="section" style="margin-top: 0px;">退回原因</div>
        <div style="padding: 10px 32px;line-height: 1.7;">{{back}}</div>
      </div>
      <div class="patient-base">
        <div class="section" v-if="back">基本信息</div>
        <div class="section" style="margin-top: 0px;" v-else>基本信息</div>
        <p><span class="name">姓名</span><span v-text="patientBase.u_patientname"></span></p>        
        <p><span class="name">性别</span><span v-text="patientBase.u_gender === '1' ? '男' : patientBase.u_gender === '2' ? '女' : ''"></span></p>
        <p><span class="name">出生日期</span><span v-text="patientBase.u_birthday"></span></p>
        <p><span class="name">手机号</span><span v-text="patientBase.u_phone"></span></p>
        <p><span class="name">备用电话</span><span v-text="patientBase.u_secondphone"></span></p>
        <p><span class="name">入组时间</span><span v-text="patientBase.u_jointime"></span></p>
        <p><span class="name">地址</span><span v-text="patientBase.u_address"></span></p>
      </div> 
      <div class="patient-records">
        <div class="item" v-for="item in patientData">
          <div class="section" v-if="item.title === 'SECTION'">{{ item.answer }}</div>
          <div v-else-if="item.title === 'FORM'"></div>
          <p class="question" v-else>
            <span class="question-name">{{item.title}}</span>
            <span v-if="item.answer.indexOf('ufile')"><a :href="'http://' + item.answer.split(',')[1]" target="_blank">{{item.answer.split(',')[0]}}</a></span>
            <span v-else>{{item.answer}}</span>
          </p>
        </div>
      </div>
      <!--备注说明列表-->
      <div class="text-note">
        <p class="section">备注说明</p>
        <div class="text-note-item" v-for="item, index in textNoteList">
          <p class="text-note-content">{{ item.remark }}</p>
          <p class="text-note-log flex-row">
            <span>{{ item.s_username }} {{ item.createtime }}</span>
            <img style="cursor: pointer;" src="../assets/icon_svg/icon_delete.svg" @click="deleteTextNote(item.id, index)">
          </p>
        </div>
      </div>
      <!--end-->
      <!--文档附件及说明列表-->
      <div class="section text-note">
        <p class="title">文档附件</p>
        <div class="text-note-item" v-for="item, index in fileList">
          <p class="text-note-content  flex-row">
            <span class="filename">{{ item.name }}</span>
            <span style="color: rgba(0,0,0,.54);">{{ item.remark }}</span>
          </p>
          <p class="text-note-log flex-row">
            <span>{{ item.s_username }} {{ item.createtime }}</span>
            <img style="cursor: pointer;" src="../assets/icon_svg/icon_delete.svg" @click="deleteFile(item.id, index)">
          </p>
        </div>
      </div>
      <!--end-->
    </div>

    <!--章节标题栏-->
    <div class="right-menu" :style="{left: left + 'px', top: top + 'px'}">
      <p :class="{active: item.flag}" v-for="item, index in section" @click="scrollEvent(index)">
        {{item.title}}
        <span v-if="item.title === '备注说明'">（{{ textNoteList.length }}）</span>
        <span v-if="item.title === '文档附件'">（{{ fileList.length }}）</span>
      </p>
    </div>
  </div>
</template>
<script>
  import API from '../api.js'
  import { Type, throttle, scrollTo } from '../utils/tools.js'
  import DialogBox from '../lib/dialog.vue'
  import Dropdown from '../lib/Dropdown.vue'
  import MInput from '../lib/input.vue'
  export default {
    data () {
      return {
        // template-menu position
        width: '',
        left: '',
        top: '',
        status: '', // 病例状态
        currentTaskId: '',
        patientBase: {},
        taskList: [],
        section: [],
        patientData: [],
        back: '',
        textNoteList: [],
        fileList: [],
        backDialog: false, // 退回病例
        backText: '',
        interval: null, // 定时器
        scrollTopVal: 0, // 文档滚动高度
        offsetTopVal: 0 // 记录点击之后的当前章节的offsetTop
      }
    },
    components: {
      Dropdown,
      MInput,
      DialogBox
    },
    mounted () {
      // template-menu 的left 和 top 值
      this.width = document.getElementById('patient-detail').offsetWidth - 240 - 16
      this.left = document.getElementById('patient-taskform').offsetLeft + this.width + 16
      this.top = document.getElementById('patient-taskform').offsetTop
      window.addEventListener('resize', () => {
        if (!document.getElementById('patient-taskform')) { return }
        this.width = document.getElementById('patient-detail').offsetWidth - 240 - 16
        this.left = document.getElementById('patient-taskform').offsetLeft + this.width + 16
      }, false)
      this.getPatientBase()
      this.getPatientRecord()
      this.getNoteList()
      this.getFileList()
      this.getTaskList()
      //
      scrollTo(document.body, 0, 300)
      window.addEventListener('scroll', (e) => {
        let vm = this
        if (vm.interval === null) {
          vm.interval = setInterval(function () {
            // 定时判断页面滚动是否结束
            if (document.body.scrollTop === vm.scrollTopVal) {
              throttle(vm.scrollEvent(), 1000)
              if (vm.offsetTopVal > vm.scrollTopVal) {
                vm.offsetTopVal = 0
              }
              clearInterval(vm.interval)
              vm.interval = null
            } else {
              vm.scrollTopVal = document.body.scrollTop
            }
          }, 100)
        }
      }, false)
    },
    methods: {
      // 页面滚动
      scrollEvent (index) {
        if (this.section.length !== 0) {
          // 点击章节事件
          if (Type(index) === 'number') {
            for (let i in this.section) {
              this.section[i].flag = false
            }
            this.section[index].flag = true
            this.offsetTopVal = document.getElementsByClassName('section')[index].offsetTop - 168
            scrollTo(document.body, this.offsetTopVal, 300)
          } else {
            // 滚动事件
            if (this.scrollTopVal < this.offsetTopVal) return
            for (let j = 0; j < document.getElementsByClassName('section').length; j++) {
              if (this.scrollTopVal >= document.getElementsByClassName('section')[j].offsetTop - 350) {
                for (let i in this.section) {
                  this.section[i].flag = false
                }
                this.section[j].flag = true
              }
            }
          }
        }
      },
      /* patientBase */
      getPatientBase () {
        API.GetPatientBase({mdid: this.$route.params.MDID}).then((response) => {
          this.patientBase = response[0]
        }).catch((err) => {
          window.alert(err)
        })
      },
      /* tasklist */
      getTaskList () {
        API.GetTask({projectid: this.$route.params.projectid, userid: this.$root.userid}).then((response) => {
          this.taskList = response
          this.currentTaskId = this.taskList[0].taskid
        }).catch((err) => {
          window.alert(err)
        })
      },
      // 患者病例数据
      getPatientRecord () {
        API.GetPatientRecord({mdid: this.$route.params.MDID}).then((response) => {
          this.patientData = JSON.parse(response[0].patientdata)
          this.status = response[0].status
          this.back = response[0].remark
          this.rightMenu()
        }).catch((err) => {
          console.log(err)
        })
      },
      // 右侧菜单栏
      rightMenu () {
        this.section = [{title: '基本信息', flag: true}]
        for (let sid in this.patientData) {
          if (this.patientData[sid].title === 'SECTION') {
            this.section.push({ title: this.patientData[sid].answer, flag: false })
          }
        }
        this.section.push({title: '备注说明', flag: false})
        this.section.push({title: '文档附件', flag: false})
      },
      // 获取患者备注
      getNoteList () {
        API.GetNoteList({mdid: this.$route.params.MDID}).then((response) => {
          this.textNoteList = response
        }).catch((err) => {
          console.log(err)
        })
      },
      // 获取文件列表
      getFileList () {
        API.GetFileList({patientid: this.$route.params.MDID}).then((response) => {
          this.fileList = response
        }).catch((err) => {
          console.log(err)
        })
      },
      // 病历审核 通过
      checkPass (status) {
        let vm = this
        this.confirm({
          title: '审核病历',
          message: '确定该病例通过审核？',
          onConfirm () {
            API.CheckRecord({userid: vm.$root.userid, mdid: vm.$route.params.MDID, projectid: vm.$route.params.projectid, status: status, remark: vm.backText}).then((response) => {
              vm.toast({
                type: 'success',
                text: '操作成功！',
                placement: 'top'
              })
              window.history.go(-1)
            }).catch(() => {
              vm.toast({
                type: 'error',
                text: '网络异常，重新删除！',
                placement: 'top'
              })
            })
          }
        })
      },
      checkBack (status) {
        API.CheckRecord({userid: this.$root.userid, mdid: this.$route.params.MDID, projectid: this.$route.params.projectid, status: status, remark: this.backText}).then((response) => {
          this.toast({
            type: 'success',
            text: '操作成功！',
            placement: 'top'
          })
          window.history.go(-1)
        }).catch(() => {
          this.toast({
            type: 'error',
            text: '网络异常，重新删除！',
            placement: 'top'
          })
        })
      }
    }
  }
</script>
<style scoped>
  .hearder {
    position: fixed;
    z-index: 2;
    background: #eee;
    width: 100%;
    justify-content: space-between;
    padding: 20px 0;
  }
  .task {
    height: 32px;
    margin-right: 20px;
  }
  .check{
    align-items: center;
  }
  .check .tool {
    width: 120px;
    display: flex;
    justify-content: space-between;
    padding: 0 8px;
    background: #fff;
  }
  .check li{
    cursor: pointer;
    padding: 6px;
  }
  .check li:hover {
    background: rgba(70,141,241,.26)
  }
  .body {
    padding: 0 32px;
  }
  .body span {
    margin-bottom: 10px;
  }
  .task span{
    border: 1px solid rgba(0,0,0,.12);
    width: 100px;
    text-align: center;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    height: 32px;
    line-height: 30px;
    background: #fff;
    cursor: pointer;
  }
  .task span.active {
    background: #458df1;
    color: #fff;
    border-color: #458df1;
  }
  /* 基本信息 */
  .patient-taskform {
    margin-right: 16px;
    margin-top: 72px;
  }
  .patient-base {
    margin-bottom: 10px;
  }
  .patient-base, .item{
    background: #fff;
  }
  .patient-base p, .question{
    height: 40px;
    display: flex;
    align-items: center;
  }
  .name {
    width: 140px;
    text-align: right;
    margin-right: 32px;
    color: rgba(0,0,0,.54);
  }
  .question-name {
    margin: 0 32px;
    color: rgba(0,0,0,.54);
  }
  .section {
    border-bottom: 1px rgba(0,0,0,.12) solid;
    height: 60px;
    font-size: 20px;
    color: rgba(0,0,0,.87);
    display: flex;
    align-items: center;
    padding: 0 32px;
    margin-top: 20px;
  }
  .text-note {
    padding: 0;
    background: #fff;
    margin-top: 30px;
  }
  .text-note .title{
    font-size: 20px;
    height: 60px;
    line-height: 58px;
    padding-left: 32px;
    border-bottom: 2px #ddd solid;
  }
  .text-note .text-note-item {
    padding: 24px 32px 16px;
    border-bottom: 1px #ddd solid;
  }
  .text-note-item .text-note-content {
    margin-bottom: 20px;
    color: rgba(0,0,0,.87);
  }
  .text-note-item .text-note-log {
    color: rgba(0,0,0,.26);
    font-size: 12px;
    justify-content: space-between;
  }
  .text-note-item .filename {
    width: 250px;
    margin-right: 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  
  .card-remark{
    padding-left: 10px;
    margin-bottom: 10px;
    border-left: 3px solid #3cb6ec;
    color: rgba(0,0,0,.54);
    font-weight: bold;
    font-size: 15px;
  }
  .card-imgs {
    padding: 0 32px;
  }
  .card-imgs .small-img {
    width: 80px;
    height: 80px;
    margin-left: 12px;
    margin-bottom: 10px;
    cursor: pointer;
  }
  .imgDialog{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999;
    background: rgba(0,0,0,.5);
    width: 100%;
    height:  100%;
    display: flex;
    justify-content: center;
    align-items: center; 
  }
  .card-imgs .large-img{
    width: 600px;
  }
  .right-menu {
    width: 240px;
    background: #fff;
    position: fixed;
    z-index: 1;
  }
  .right-menu p{
    height: 40px;
    cursor: pointer;
    text-align: center;
    line-height: 40px;
  }
  .right-menu p.active {
    background: rgba(70, 141, 241, .24);
  }
  .back-record {
    width: 196px;
    margin-bottom: 10px;
  }
</style>


