import Vue from 'vue'
import VueRouter from 'vue-router'
/** all link */
import App from './App'
import Login from './views/Login'
import Home from './views/Home'
import Project from './views/Project.vue'
import ProjectHome from './views/Projecthome.vue'
import PatientList from './views/Patientlist.vue'
import PatientDetail from './views/Patientdetail.vue'
import MemberManagement from './views/Membermanagement.vue'
import CenterManagement from './views/Centermanagement.vue'
import FollowPlan from './views/Followplan.vue'
import Notification from './views/Notification.vue'
import User from './views/User.vue'
import UserInfo from './views/Userinfo.vue'
import ResetAvatar from './views/Resetavatar.vue'
import ResetPw from './views/Resetpw.vue'

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'hash',
  routes: [
    {
      path: '/',
      component: App,
      redirect: {name: 'home'}, //  重定向
      meta: {requiresAuth: true, title: '首页'}
    },
    {
      name: 'home',
      path: '/home',
      component: Home,
      meta: {requiresAuth: true, title: '首页'}
    },
    {
      path: '/project/:projectid',
      component: Project,
      meta: {requiresAuth: true},
      children: [
        {
          path: 'project_home',
          component: ProjectHome,
          meta: {requiresAuth: true, title: '项目首页'}
        },
        {
          path: 'patient_list',
          component: PatientList,
          meta: {requiresAuth: true, title: '患者病例列表'}
        },
        {
          path: 'patient_list/:groupid/:pagenum',
          name: 'patientList',
          component: PatientList,
          meta: {requiresAuth: true, title: '患者病例列表'}
        },
        {
          path: 'patient_list/:groupid/:pagenum/patient_detail/:MDID',
          component: PatientDetail,
          name: 'patientDetail',
          meta: {requiresAuth: true, title: '患者详情'}
        },
        {
          path: 'member_management',
          component: MemberManagement,
          meta: {requiresAuth: true, title: '成员管理'}
        },
        {
          path: 'center_management',
          component: CenterManagement,
          meta: {requiresAuth: true, title: '分中心管理'}
        },
        {
          path: 'follow_plans',
          component: FollowPlan,
          meta: {requiresAuth: true, title: '随访计划列表'}
        }
      ]
    },
    {
      path: '/notification',
      component: Notification,
      meta: {requiresAuth: true, title: '通知'}
    },
    {
      path: '/user',
      component: User,
      meta: {requiresAuth: true, title: '用户'},
      children: [
        {
          path: 'userInfo',
          component: UserInfo,
          meta: {requiresAuth: true, title: '个人信息'}
        },
        {
          path: 'resetAvatar',
          component: ResetAvatar,
          meta: {requiresAuth: true, title: '头像修改'}
        },
        {
          path: 'resetPw',
          component: ResetPw,
          meta: {requiresAuth: true, title: '密码修改'}
        }
      ]
    },
    {
      path: '/login',
      component: Login,
      meta: {title: '登录'}
    },
    { path: '*', redirect: '/home' }
  ]
})

export default router



