<template>
  <div id="nav" class="flex-row">
    <div class="flex-row left">
      <img src="../assets/header_svg/logo.svg" alt="Medicayun" class="logo">
      <router-link to='/home' class="appname">临床研究管理</router-link>
    </div>
    <div class="flex-row right">
      <router-link to='/notification' class="iconfont icon-tongzhi" tag="i"><span v-if="nodispose" class="notice"></span></router-link>
      <i class="iconfont icon-caidan-copy" @click.stop="changeCloud"></i>
      <div class="user"  @click.stop="showConfig">
        <template v-if="$root.userInfo.s_avatar">
          <img :src="'http://' + $root.userInfo.s_avatar" alt="userImg" class="small-avatar">
        </template>
        <span class="small-avatar" v-else>{{ $root.shortName.substring(0, 3) | uppercase}}</span>
      </div>
    </div>
    <Popover ref="config" class="config flex-col">
      <router-link to="/user/userInfo" class="user-info flex-row">
        <span class="large-avatar">{{ $root.shortName.substring(0, 3) | uppercase }}</span>
        <div class="flex-col" style="flex: 1;">
          <span class="user-name">{{ $root.userInfo.s_username }}</span>
          <span class="user-work-unit">{{ $root.userInfo.s_workunti }}</span>
        </div>
      </router-link>
      <div class="link-list flex-col">
        <router-link to="/user/userInfo" class="link-item">用户设置</router-link>
        <router-link to="/user/userInfo" class="link-item">用户帮助</router-link>
      </div>
      <div class="exit-btn"><div class="btn-gray" @click="exit">退出</div></div>
    </Popover>
    <popover ref="cloud" class="cloud flex-row">
      <div class="flex-col link-apply" @click="go('mobilehome')">
        <img src="../assets/header_svg/icon_mednotes.svg" alt="mednotes">
        <span>患者登记</span>
      </div>
      <div class="flex-col link-apply" @click="go('inputhome')">
        <img src="../assets/header_svg/icon_mednotes.svg" alt="mednotes">
        <span>新患者登记</span>
      </div>
      <div class="flex-col link-apply" @click="go('pchome')">
        <img src="../assets/header_svg/icon_manage.svg" alt="manage">
        <span>临床研究管理</span>
      </div>
      <div class="flex-col link-apply" @click="go('eform')">
        <img src="../assets/header_svg/icon_manage.svg" alt="manage">
        <span>报告表编辑</span>
      </div>
      <div class="flex-col link-apply">
        <img src="../assets/header_svg/icon_imaging.svg" alt="imaging">
        <span style="color: rgb(170, 170, 170)">影像中心</span>
      </div>
    </popover>
  </div>
</template>
<script>
  import API from '../api.js'
  import MButton from '../lib/button.vue'
  import Popover from '../lib/Popover.vue'
  export default {
    name: 'Nav',
    data () {
      return {
        nodispose: false // 新消息提醒
      }
    },
    components: {
      MButton,
      Popover
    },
    mounted () {
      this.getNoticeList()
      this.getUserInfo()
    },
    methods: {
      // 获取通知列表
      getNoticeList () {
        API.GetNotices({userid: this.$root.userid}).then(response => {
          if (!response.length) { return }
          for (let n in response) {
            if (n.status === '0') {
              this.nodispose = true
              break
            }
          }
        }).catch(err => {
          window.alert(err)
        })
      },
      // 获取用户信息
      getUserInfo () {
        API.GetUserInfo({userid: this.$root.userid}).then(response => {
          this.$root.userInfo = response[0]
          this.nameToshort(response[0].s_username)
        }).catch((err) => {
          window.alert(err)
        })
      },
      // 获取用户姓名首字母缩写
      nameToshort (str) {
        API.ChineseToShort({string: str}).then(response => {
          this.$root.shortName = response.shortpinyin
        }).catch(err => {
          console.log(err)
        })
      },
      // 显示用户配置dialog
      showConfig (event) {
        this.$refs.config.show(event.currentTarget)
        this.$refs.cloud.hide()
      },
      // 显示云dialog
      changeCloud (event) {
        this.$refs.cloud.show(event.currentTarget)
        this.$refs.config.hide()
      },
      go (siteAddress) {
        window.location.href = '/user/' + siteAddress
      },
      /* 退出 */
      exit () {
        window.sessionStorage.clear()
        if (process.env.NODE_ENV === 'development') {
          this.$router.push({path: '/login'})
        } else {
          window.location.href = window.location.origin + '/?redirect=pc'
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  /* 导航条固定定位 */
  #nav {
    background: #468df1;
    width: 100%;
    height: 56px;
    justify-content: space-between;
    position: fixed;
    font-size: 18px;
    color: #fff;
    top: 0;
    left: 0;
    z-index: 999;
  }
  .left, .right{
    align-items: center;
  }
  .left .logo {
    width: 40px;
    height: 32px;
    margin-left: 12px;
    margin-right: 14px;
  }
  .left .appname {
    font-size: 18px;
    color: #fff;
  }
  .right i.iconfont{
    cursor: pointer;
    display: inline-block;
    height: 56px;
    line-height: 56px;
    width: 56px;
    text-align: center;
  }
  .right i.iconfont:hover {
    background: rgba(255, 255, 255, .2);
  }
  .right .notice{
    position: absolute;
    display: block;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background: #f00;
    right: 7px;
    top: 5px;
  }
  .right .user {
    width: 56px;
    height: 56px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .right .small-avatar {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background: #fff;
    text-align: center;
    line-height: 32px;
    font-size: 12px;
    color: #000;
  }

  .config .user-info{
    height: 102px;
    width: 300px;
    padding: 0 24px;
    border-bottom: 1px solid rgb(227, 227, 227);
    background: #fafafa;
    align-items: center;
  } 
  .config .user-info .large-avatar{
    width: 70px;
    height: 70px;
    background: rgb(70, 141, 241);
    line-height: 70px;
    color: #fff;
    font-size: 22px;
    margin-right: 12px;
    border-radius: 50%;
    text-align: center;
  }
  .config .user-info .user-name {
    font-size: 18px;
    line-height: 24px;
  }
  .config .user-info .user-work-unit {
    overflow : hidden;
    font-size: 14px;
    line-height: 24px;
  }
  .config .link-list{
    color: rgb(68, 68, 68);
    margin: 0 12px;
    padding: 12px;
    border-bottom: 1px solid rgb(227, 227, 227);
  }
  .config .link-list a.link-item {
    font-size: 18px;
    line-height: 2;    
  }
  .config .link-list a:hover{
    color: rgb(70, 141, 241)
  }
  .config .exit-btn {
    padding: 12px;
    text-align: right;
    font-size: 14px;
  } 
  
 .cloud{
   width: 338px;
   flex-wrap: wrap;
 }
 .cloud .link-apply {
   width: 90px;
   align-items: center;
   cursor: pointer;
   border: 1px solid transparent;
   padding: 8px 5px;
   margin: 15px 10px;
 }
 .cloud .link-apply:hover {
   border-color: #ddd;
 }
 .cloud .link-apply span {
   font-size: 13px;
   text-align: center;
   margin-top: 14px;
 }
</style>



